<template>
     <div class="usersList">


          <div class="user " v-for="user in this.$store.getters.USERS" :key="user.id">
               <div class="userMain">
                    <p> {{ user.name }} </p>
                    <button @click="toOpen(user.id)">
                         <span v-if="isOpen !== user.id "><img src="../assets/plus-svgrepo-com.svg" alt="+"></span>
                         <span v-else><img src="../assets/minus-svgrepo-com.svg" alt="-"></span>
                    </button>
               </div>

               <div class="userInfo" v-if='isOpen === user.id'>
                    <div v-for="(info, index) in user" :key="info.name">

                         <p v-if="(typeof info === 'object')"><!--Если значение объект, то перебираем и выводим его-->
                              <span class="secondary">{{ index }}:</span><br>
                              <span v-for="(info2, index2) in info" :key="info2.street">

                                   <p v-if="(typeof info2 === 'object')"> <!--Если подзначение также объект, то перебираем и выводим его-->
                                        <span class="secondary">{{ index2 }}:</span><br>

                                        <span v-for="(info3, index3) in info2" :key="info3">
                                             <p><span class="secondary">{{ index3 }}: </span> <span>{{ info3 }}</span></p>
                                        </span>
                                   </p>

                                   <p v-else><span class="secondary">{{ index2 }}: </span> <span>{{ info2 }}</span></p> <!--Если подзначение не объект, то выводим его-->

                              </span> 
                         </p>

                         <p v-else> <!--Если значение не объект, то выводим его-->
                              {{ index }}: 
                                   <a v-if="index === 'website'" :href="'http://'+ info" target="_blank"><span>{{ info }}</span></a>
                                   <span v-else>{{ info }}</span>
                         </p>


                    </div>
               </div>
          </div>

     </div>
</template>


<script>
     import { mapActions } from 'vuex';

     export default {
          data() {
               return {
                    isOpen: false, //видимость подробных данных о пользователе
               }
          },
          mounted() {
               this.GET_USERS_FROM_API() //вызов функции получения данных о пользователях из API
               this.toOpen(this.$store.getters.CLICKED_ID)
          },
          methods: {
               ...mapActions([ //получние функции из хранилища
                    'GET_USERS_FROM_API' 
               ]),
               toOpen(id) {  //открытие подробных данных о пользователе 
                    this.isOpen === id ? this.isOpen = false : this.isOpen = id
               }
          },

     }
</script>


<style>
     .usersList {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          justify-content: center;

          padding: 15px 0;
          transition: all 0.9s;
     }
     .userMain {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          width: 55vw;
          box-shadow: 0 0 11px rgba(122, 122, 122, 0.342);
          border-radius: 20px;
          padding: 8px 15px;
          background-color: white;
          margin: 5px 21px 11px 0;
          font-weight: bold;
          font-size: 21px;
     }
     button {
          border-radius: 30px;
          background-color: white;
          box-shadow: 0 0 10px rgba(46, 46, 46, 0.342);
          border: none;
          cursor: pointer;
          font-size: 25px;
          width: 40px;
          height: 40px;
          font-weight: bold;
          transition: all 0.2s;
          display: flex;
          justify-content: center;
          align-items: center;
     }
     button:hover {
          background-color: rgb(236, 236, 236);
          transition: all 0.2s;
     }
     button span {
          display: flex;
          justify-content: center;
          align-items: center;
     }
     button span img{
          height: 25px;
     }
     .userInfo {
          width: 55vw;
          box-shadow: 0 0 11px rgba(122, 122, 122, 0.342);
          border-radius: 20px;
          background-color: rgb(228, 228, 228);
          padding: 15px;
          transition: all ease 3s;
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          margin-bottom: 7px;
     }
     .userInfo p {
          display: inline-block;
          padding: 7px;
          margin: 9px;
          border-radius: 10px;
          box-shadow: 0 0 11px rgba(122, 122, 122, 0.342);
          color: rgb(88, 88, 88);
          background-color: white;
          font-style: italic;
     }
     .secondary {
          font-style: italic !important;
          font-weight: normal !important;
     }
     .userInfo p span {
          font-style: normal;
          font-weight: bold;
     }
</style>